@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto+Slab:wght@200&display=swap);

:root {
	--color-light: #fcfcfc;
	--color-dark: #2f2f2f;
	--color-darker: #0f0f0f;
	--side-width: 30%;
	--side-minWidth: 350px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: "Roboto Slab", serif;
	color: #2f2f2f;
	color: var(--color-dark);
}

.screen {
	height: 100vh;
	background: #fcfcfc;
	background: var(--color-light);
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

h1,
h2,
h3,
h4 {
	font-family: "Open Sans", sans-serif;
	text-transform: uppercase;
}

p + p {
	margin-top: 1rem;
}

nav {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 2;
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	width: 100%;
}

.logo-container {
	display: flex;
	width: 100%;
}

.logo {
	font-size: 3.5rem;
	line-height: 1.1;
	color: #fcfcfc;
	color: var(--color-light);
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	padding: 0 1rem;
	width: 100%;
}

.nav-links {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	max-width: 500px;
	flex: 1 1 100%;
	padding: 0.5rem 1rem;
	font-size: 1.4rem;
	list-style: none;
	background: #fcfcfc;
	background: var(--color-light);
	position: relative;
}

a,
.nav-link {
	text-decoration: none;
	color: #2f2f2f;
	color: var(--color-dark);
	position: relative;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.more-button::after,
.post-link::after,
.nav-link::after {
	content: "";
	position: absolute;
	width: 0;
	bottom: -0.25rem;
	left: 0;
	right: 0;
	border-bottom: 1px solid #2f2f2f;
	border-bottom: 1px solid var(--color-dark);
	transition: width 150ms ease-in-out;
}

.more-button:hover::after,
.more-button:focus::after,
.post-link:hover::after,
.post-link:focus::after, 
.nav-link:hover::after,
.nav-link:focus::after {
	width: 100%;
}

.link-button,
.more-button {
	font-size: 1.4rem;
	font-family: "Roboto Slab", serif;
	color: #2f2f2f;
	color: var(--color-dark);
	background: none;
	border: none;
	cursor: pointer;
}

.social-nav {
	position: absolute;
	display: flex;
}

main > .social-nav {
	top: 0;
	bottom: 0;
	z-index: 2;
	flex-direction: column;
	justify-content: center;
	margin: 0.75rem;
}

.container-right > .social-nav {
	bottom: 0;
	left: 0;
	right: 0;
	justify-content: space-around;
	-webkit-filter: invert(1);
	        filter: invert(1)
}

.social-nav-link {
	height: 45px;
	width: 45px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0.5rem 0;
}
.social-nav-img {
	height: 30px;
	width: 30px;
	opacity: 0.5;
	transition: all 150ms ease-in-out;
}

.social-nav-img:hover,
.social-nav-img:focus {
	opacity: 1;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

main {
	flex: 1 1 100%;

	overflow-y: scroll;
	-ms-scroll-snap-type: y mandatory;
	    scroll-snap-type: y mandatory;
	-ms-overflow-style: none;
	scrollbar-width: none;
}

section {
	display: flex;
	height: 100%;
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	scroll-snap-align: start;
}

main::-webkit-scrollbar {
	display: none;
}

.container-left {
	width: 100%;
	display: flex;
	position: relative;
}

.header-container {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	width: 100%;
	height: 45%;
	z-index: 0;
}

.header-image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.container-right {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 500px;
	flex: 1 1 100%;
	background-image: linear-gradient(
		180deg,
		#fcfcfc 10%,
		rgba(255, 255, 255, 0.7)40% 60%,
		#fcfcfc 90%
	);
	background-image: linear-gradient(
		180deg,
		var(--color-light) 10%,
		rgba(255, 255, 255, 0.7)40% 60%,
		var(--color-light) 90%
	);
	border-left: 1px solid #fcfcfc;
	border-left: 1px solid var(--color-light);
	box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.5);
	-webkit-backdrop-filter: blur(16px);
	        backdrop-filter: blur(16px);
}

.content-container {
	padding: 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	/* max-height: 100%; */
	background: linear-gradient(
		180deg,
		rgb(0 0 0 / 0) 10%,
		#0f0f0f 40%
	);
	background: linear-gradient(
		180deg,
		rgb(0 0 0 / 0) 10%,
		var(--color-darker) 40%
	);
	z-index: 1;
}
.content-block {
	color: #fcfcfc;
	color: var(--color-light);
	text-align: justify;
	width: 100%;
	padding: 1rem;
	
	overflow: scroll;
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.blockContent {
	max-width: 80ch;
	margin: 0 auto;
}

.blockContent h1,
.blockContent h2,
.blockContent h3,
.blockContent h4,
.blockContent a {
	margin: 1rem 0;
}

.content-block::-webkit-scrollbar {
	display: none;
}

.content-block a {
	color: #fcfcfc;
	color: var(--color-light);
	font-weight: bold;
	text-decoration: underline;
}

/* HOME PAGE */

.hero-image-link {
	width: 100%;
	height: 100%;
	background-image: linear-gradient(
		0deg,
		rgb(0 0 0 / .8)0%,
		rgb(0 0 0 / 0)35% 65%,
		rgb(0 0 0 / .8)100%
	);
}

.hero-image {
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: none;
}

.post-description {
	margin-top: auto;
	display: flex;
	flex-direction: column;
	padding: .5rem 1rem;
	font-size: 1.2rem;
}

.post-date {
	font-size: 1rem;
}
.post-link {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	align-self: flex-end;
	z-index: 2;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.post-excerpt {
	margin: 1rem 0;
}

.text-container {
	position: relative;
	align-self: flex-end;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.hero-title {
	text-align: right;
	font-size: 8.75rem;
	line-height: 1;
	white-space: nowrap;
}

.hero-post-number {
	font-size: 1.3em;
}

.before {
	position: absolute;
	left: 0;
	bottom: 0;
	color: #fcfcfc;
	overflow: hidden;
}

/* JOURNAL LIST */

.journal-container {
	position: absolute;
	top: 100%;
	right: 0;
	left: 0;
	width: 100%;
	height: calc(100vh - 100%);
	z-index: -1;
	background: rgb(0 0 0 / 0.7);
	-webkit-backdrop-filter: blur(16px);
	        backdrop-filter: blur(16px);

	overflow: scroll;
	-ms-overflow-style: none;
	scrollbar-width: none;

	display: none;
}

.journal-container::-webkit-scrollbar {
	display: none;
  }

.show {
	display: block;
}

.post-preview * {
	color: #fcfcfc;
	color: var(--color-light)
}

.post-preview {
	padding: 1rem;
}

.post-preview > .post-link::after {
	border-bottom: 1px solid #fcfcfc;
	border-bottom: 1px solid var(--color-light);
}


.post-preview-title {
	display: inline-block;
	width: 50%;
}

.post-number {
	font-size: 1.3em;
}

.post-more {
	display: inline-block;
	text-align: right;
	font-size: 1.2rem;
	width: 50%;
}

.close-btn {
	position: absolute;
	top: 0;
	right: 0;
	width: 35px;
	height: 35px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #fcfcfc;
	background: var(--color-light);
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

/* JOURNAL POST */

.post-content {
	background-size: 200%;
	background-position: center top;
	background-repeat: no-repeat;
}

.post-margin {
	padding: 35vh 1rem 0rem 1rem;
}

.post-right {
	justify-content: center;
}

.light-title {
	color:rgb(125 125 125);
	align-self: center;
}

.vertical {
	-webkit-writing-mode: vertical-rl;
	    -ms-writing-mode: tb-rl;
	        writing-mode: vertical-rl;
    -webkit-text-orientation: mixed;
            text-orientation: mixed;
	font-size: 10vh;
}

.mobile-image {
	display: none;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.mobile-title {
	display: none;
}

/* PAGES */
.page-section, .post-section {
	scroll-snap-align: none;
	overflow: hidden;
}

.page-left {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	color: #fcfcfc;
	color: var(--color-light);
	font-size: 1.2rem;
	background-color: #2f2f2f;
	background-color: var(--color-dark);
	position: relative;
}

.page-right {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 3.8rem;
	min-height: calc(100vh - 3.8rem);
	border: none;
	max-height: calc(100vh - 3.8rem);
}

.profile-block {
	display: flex;
	align-items: center;
	grid-gap: 1rem;
	gap: 1rem;
	padding: 1rem;
}

.profile-pic {
	width: 150px;
	height: 150px;
	object-fit: cover;
	border-radius: 50%;
	box-shadow: 0px 0px 20px rgb(0 0 0 / .2);
}

.bio {
	padding: 1rem;
	margin-bottom: 3.8rem;
	overflow: scroll;
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.bio::-webkit-scrollbar {
	display: none;
  }

.bio h2 {
	margin-bottom: 1rem;
}

.fixed-container {
	overflow: hidden;
}

.minimized {
	display: -webkit-box;
	-webkit-line-clamp: 10;
	-webkit-box-orient: vertical;  
	overflow: hidden;
}

.more-button {
	margin-top: 1rem;
}

.page-title {
	position: absolute;
	bottom: 0;
	right: 1rem;
	line-height: 1;
	font-size: 10vw;
	opacity: .05;
}

.about-block p:last-child {
	font-size: 1.3rem;
	margin-bottom: 1rem;
	text-align: right;
}

/* CONTACT PAGE */

.form-wrapper {
	justify-content: center;
	align-items: center;
}

.contact-form {
	padding: 1rem;
	border-radius: 3px;
	background: rgb(255 255 255 / .7);
	box-shadow: 0px 0px 30px rgb(0 0 0 / .3);
	-webkit-backdrop-filter: blur(1rem);
	        backdrop-filter: blur(1rem);
	max-width: 50ch;
}

.form-header {
	margin: 1rem 0;
}

.contact-form h2 {
	font-size: 2.5em;
	text-align: center;
}

.contact-form p {
	text-align: center;
	font-size: 1.2rem;
}

label {
	display: block;
	font-size: 1.5rem;
	margin-bottom: .25rem;
}

input {
	display: block;
	width: 100%;
	height: 2rem;
	margin-bottom: 1rem;
	border: 1px solid rgb(0 0 0 / .2);
	background: rgb(255 255 255 / .5);
	padding: .5rem;
}

textarea {
	width: 100%;
	resize: none;
	margin-bottom: 1rem;
	border: 1px solid rgb(0 0 0 / .2);
	background: rgb(255 255 255 / .5);
	padding: .5rem;
}

.btn-container {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 1rem;
}

.submit-button {
	font-family: "Open Sans", sans-serif;
	font-size: 1.25rem;
	text-transform: uppercase;
	color: #fcfcfc;
	color: var(--color-light);
	background: #2f2f2f;
	background: var(--color-dark);
	padding: .5rem 2rem;
	border: none;
	border-radius: 3px;
	cursor: pointer;
}

@media only screen and (max-width: 1024px) {
	main > .social-nav {
		display: none;
	}

	.container-right > .social-nav {
		bottom: auto;
		top: 0;
	}

	nav {
		height: auto;
		position: static;
		flex-wrap: wrap;
		box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.5);
	}

	.logo-link {
		margin: 0 auto;
	}

	.logo {
		color: #2f2f2f;
		color: var(--color-dark);
	}

	.nav-links {
		margin: 0 auto;
	}

	section {
		flex-wrap: wrap;
	}

	.container-left {
		flex: 1 2 auto;
	}

	.post-section {
		overflow: visible;
	}

	.post-content {
		flex-direction: column;
		overflow: scroll;
	}

	.header-container {
		height: 40vh;
		position: relative;
	}

	.header-title {
		font-size: 5vh;
		text-align: center;
		padding: .25rem .5rem;
		text-shadow: 0px 0px 30px black;
		background-image: linear-gradient(rgb(0 0 0 / 0), rgb(0 0 0 / .3));
	}

	.mobile-title {
		display: block;
		position: absolute;
		bottom: 0;
		right: 0;
		left: 0;
	}

	.content-block {
		overflow: hidden;
	}

	.content-container {
		background: #fcfcfc;
		background: var(--color-light);
	}

	.blockContent {
		color: #2f2f2f;
		color: var(--color-dark);
	}

	.about-block {
		color: #fcfcfc;
		color: var(--color-light);
	}

	.post-margin {
		padding: 0;
	}

	.hero-image {
		display: block;
	}

	.container-right {
		flex: 0 1 auto;
		max-width: 100%;
		background: linear-gradient(rgb(255 255 255 / .5), rgb(255 255 255 / .9) 30%);
		border-left: none;
		border-top: 1px solid rgb(150 150 150 / .5);
		-webkit-backdrop-filter: blur(.5rem);
		        backdrop-filter: blur(.5rem);
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
	}

	.post-description {
		margin: 0;
	}

	.post-excerpt {
		display: -webkit-box;
		-webkit-line-clamp: 5;
		-webkit-box-orient: vertical;  
		overflow: hidden;
	}

	.text-container {
		display: none;
	}

	.hero-title {
		color: #fcfcfc;
		color: var(--color-light);
		font-size: 10vw;
	}

	.before {
		display: none;
	}

	.page-section {
		overflow: visible;
	    flex-direction: column-reverse;
		flex-wrap: nowrap;
		height: auto;
	}

	.page-right {
		max-height: none;
		min-height: calc(100vh - 10.5rem);
		/* flex-direction: column; */
	}

	.content-block {
		max-height: none;
		margin: 0;
		padding: 1rem 1rem;
		max-height: none;
	}

	.page-title {
		bottom: auto;
		top: 0;
		font-size: 25vw;
	}

	.info {
		flex: 1 2;
	}

	.bio {

	}

	.post-right {
		display: none;
	}
}

